html {
  overflow: -moz-scrollbars-none;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  font-family: "Roboto Mono", sans-serif;
  font-weight: 400;
  font-size: 20px;
  background-color: var(--color-white);
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  padding: 0;
}

p {
  margin: 0;
  padding: 0;
}

ul,
ol {
  list-style: none;
  margin: 0;
  padding: 0;
}

li {
  list-style-type: none;
}

a {
  text-decoration: none;
  color: inherit;
}

img {
  max-width: 100%;
}

figure {
  margin: 0;
  padding: 0;
}

button {
  outline: none;
  cursor: pointer;
}

span {
  display: block;
}

input,
textarea {
  outline: none;
}

.container {
  margin: 0 auto;
  padding: 0 30px;
  max-width: 1920px;
}

.visually-hidden {
  position: absolute !important;
  clip: rect(1px 1px 1px 1px);
  /* IE6, IE7 */
  clip: rect(1px, 1px, 1px, 1px);
  padding: 0 !important;
  border: 0 !important;
  height: 1px !important;
  width: 1px !important;
  overflow: hidden;
}

.wrapper {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

.section-body {
  margin: 0px auto;
  width: 100%;
}

.disable-scroll {
  position: relative;
  overflow: hidden;
  width: 100%;
  height: 100vh;
}

.header {
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  padding-top: 3px;
  background-color: transparent;
}

.header__container {
  display: flex;
  justify-content: space-between;
}

.header__logo {
  z-index: 100;
  font-family: "Space Mono";
  font-weight: 700;
  font-style: italic;
  font-size: 30px;
  letter-spacing: -0.05em;
  color: var(--color-white);
}

.contact {
  z-index: 100;
  padding-top: 7px;
  text-align: right;
  color: var(--color-white);
}

.contact__text {
  margin-bottom: 8px;
}

.first {
  position: relative;
  padding-top: 36.8vh;
  height: 100vh;
  background-color: var(--color-black);
}

.first__container {
  z-index: 100;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  height: 100%;
}

.first__info {
  z-index: 100;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  max-width: 920px;
}

.first__title {
  z-index: 100;
  margin-bottom: 30px;
  font-family: "Space Mono";
  font-weight: 700;
  font-style: italic;
  font-size: 80px;
  line-height: 80px;
  text-align: center;
  text-transform: uppercase;
  color: var(--color-purple);
}

.first__descr {
  z-index: 100;
  max-width: 450px;
  line-height: 30px;
  text-align: center;
  color: var(--color-white);
}

.to-projects {
  z-index: 100;
  display: flex;
  flex-direction: column;
  align-items: center;
  align-self: flex-end;
  margin-bottom: 85px;
  width: 100%;
}

.to-projects__text {
  margin-bottom: 10px;
  color: var(--color-white);
}

.to-projects__btn {
  border: none;
  padding: 0;
  background-color: transparent;
  width: 15px;
  height: 45px;
  background-image: url(../img/arrow.svg);
  background-repeat: no-repeat;
}

.stars-bg {
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
}

