.projects {
  position: relative;
  z-index: 100;
  text-align: center;
  background-color: transparent;
}

.projects__header {
  z-index: 100;
  margin-top: -95px;
  padding-top: 150px;
  padding-bottom: 10px;
  background: linear-gradient(180deg, rgba(0, 8, 64, 0) 0%, #00052c 40.1%, #000840 100%);
  background-color: var(--color-black);
}

.projects__title {
  z-index: 100;
  font-family: "Space Mono";
  font-weight: 700;
  font-style: italic;
  font-size: 80px;
  text-transform: uppercase;
  color: var(--color-purple);
}

.other {
  z-index: 100;
  padding-bottom: 1500px;
  background-color: var(--color-white);
}
